@media screen and (max-width: 1258px) {
    .container {
      width: 90vw;
    }
    .skillCard {
      width: 45vw;
    }
    .main__content {
      width: 70vw;
    }
    .about-me__text {
      width: 100vw;
      margin-bottom: 10rem;
      text-align: center;
    }
    .latest-works__box {
      width: 90vw;
    }

    .btn {
      padding: 1.5rem 3rem;
    font-size: 3rem;
    }

    .about-me__text {
      margin-right: 0;
    }
  }
  
  @media screen and (max-width: 900px) {
    .main__content {
      width: 90vw;
    }
    .hire-btn {
      padding: 3rem 15rem;
    }
    * {
      animation: none;
    }
  } 

  @media screen and (max-height: 1000px) {
    .main, .about-me, .skills, .latest-works {
      height: 110vh;
    }
  }
  
