* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing:border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and  (max-width: 1573px) {
  html {
    font-size: 50%;
  }
}

@media screen and (max-width: 1258px) {
  html {
    font-size: 45%;
  }
} 

@media screen and (max-width: 470px) {
  html {
    font-size: 35%;
  }
} 
.App {
  text-align: center;
  -webkit-transition: all 1s;
  transition: all 1s;
  background:rgba(0, 29, 196, 0.801);
}

.container {
  margin: 0 auto;
  height: 100%;
  width: 70vw;
  position: relative;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.hidden {
  display: none;
  width: 0;
  height: 0;
}

/* MAIN */

.main {
  display: flex;
  position: relative;
  justify-content: space-around;
  align-items: stretch;
  flex-wrap: wrap;
  height: 100vh;
  background-image: 
      -webkit-linear-gradient(
      left top, 
      rgba(71, 71, 71, 0.6),
      rgba(5, 92, 255, 0.6)
      ),
       url(/static/media/160632.218b78c2.jpg);
  background-image: 
      linear-gradient(
      to right bottom, 
      rgba(71, 71, 71, 0.6),
      rgba(5, 92, 255, 0.6)
      ),
       url(/static/media/160632.218b78c2.jpg);
  background-size: cover;
  background-position: top;
  color: rgb(223, 235, 238);
}


.main__comp {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-top: 20rem;
}

.main__text p {
  font-size: 5rem;
}
.main__h{
  font-size: 5rem;
  color: rgb(255, 255, 255);
}
.main__text {
  vertical-align: center;
  -webkit-animation-name: moveInRight;
          animation-name: moveInRight;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}
.main__photo {
  width: 20vw;
  min-width: 25rem;
  vertical-align: top;
  -webkit-animation-name: moveInLeft;
          animation-name: moveInLeft;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.main__content {
  width: 50vw;
}

.hire-btn {
  margin-top: 10rem;
  text-transform: uppercase;
  background: transparent;
  padding: 3rem 30rem;
  display: inline-block;
  border-radius: 10rem;
  -webkit-transition: all .2s;
  transition: all .2s;
  position: relative;
  font-size: 2rem;
  font-weight: bold;
  border: 2px solid white;
  color: white;
  -webkit-animation: moveInBottom .5s ease-out .75s;
          animation: moveInBottom .5s ease-out .75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}

.hire-btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0 1rem 2rem rgba(0,0,0,.2);
  background: white;
  color: black;
}

.hire-btn:active {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
}

.photo { 
  border: 10px solid rgba(255, 255, 255, 0.473);
  min-width: 150px;
  width: 25rem;
  height: auto;
}

.lang {
  display: flex;
  justify-content: flex-end;
  -webkit-animation-name: moveInRight;
          animation-name: moveInRight;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.btn {
  border: 1px solid white;
  background: transparent;
  padding: 1rem 2rem;
  color:white;
  font-size: 1.5rem;
}
.en {
  margin-right: -1px;
}

.btn:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}

.active {
  background: white;
  color: black;
}
/* POP UP */ 

.pop-up {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index:100;
  background: rgba(2, 20, 39, 0.479);
  -webkit-transition: all 1s;
  transition: all 1s;
  -webkit-animation-name: backAppear;
          animation-name: backAppear;
  -webkit-animation-duration: .7s;
          animation-duration: .7s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.pop-up__window {
  padding: 5rem;
  background: white;
  border-radius: 10px;
  width: 50rem;
  height: 35rem;
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-duration: .7s;
          animation-duration: .7s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  position: relative; 
  color: black;
  font-size: 2rem;
}

.mail__box {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 3rem;
}
.mail {
  width: 2rem;
  height: auto;
  margin: 0 1rem;
}
.phone__box {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 3rem;
}
.phone {
  width: 2rem;
  height: auto;
  margin: 0 1rem;
}

.social {
  display: flex;
  justify-content: space-around;
  margin: 3rem 0;
}

span {
  display: none;
}

.close-btn {
  position: absolute;
  right: -3.2rem;
  top: -3.2rem;
  background: url(/static/media/close.8b3a11df.svg) no-repeat 50% 50%;
  width: 5rem;
  height: 5rem;
  border: none;
}
.close-btn:hover {
  background: url(/static/media/close1.e3421fe0.svg) no-repeat 50% 50%;
  cursor: pointer;
}

.vk {
  background: url(/static/media/vk.d1eff677.svg) no-repeat 50% 50%;
  width: 7rem;
  height: 7rem;
  -webkit-transition: all .6s;
  transition: all .6s;
}

.vk:hover {
  background: url(/static/media/vk1.ef15ab6a.svg) no-repeat 50% 50%;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.git {
  background: url(/static/media/github.67910a24.svg) no-repeat 50% 50%;
  width: 6rem;
  height: 6rem;
  -webkit-transition: all .6s;
  transition: all .6s;

}

.git:hover {
  background: url(/static/media/github1.db0721b1.svg) no-repeat 50% 50%;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.tw {
  background: url(/static/media/twitter.587a4df2.svg) no-repeat 50% 50%;
  width: 7rem;
  height: 7rem;
  -webkit-transition: all .6s;
  transition: all .6s;

}

.tw:hover {
  background: url(/static/media/twitter1.c9f12008.svg) no-repeat 50% 50%;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}


/* ABOUT ME */

.about-me {
  height: 100vh;
  padding-top: 5rem;
  position: relative;
  background: -webkit-linear-gradient(top, rgba(22, 45, 175, 0.753) 0%, rgb(17, 156, 236) 70%);
  background: linear-gradient(to bottom, rgba(22, 45, 175, 0.753) 0%, rgb(17, 156, 236) 70%);
}

.about-me__comp {
  height: 100%;
}

.about-me__h {
  display: block;
  margin: 0;
  padding: 0;
  font-size: 5rem;
  color:  rgb(0, 162, 255);
  -webkit-animation-name: moveInBottom;
          animation-name: moveInBottom;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.about-me__content {
  margin-top: 10rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.about-me__text {
  width: 35vw;
  margin-right: 5rem;
  font-size: 4.5rem;
  text-align: left;
  color: rgb(21, 56, 109);
  -webkit-animation-name: moveInLeft;
          animation-name: moveInLeft;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  
}

.map {
  width: 50rem;
  height: 50rem;
  -webkit-animation-name: moveInRight;
          animation-name: moveInRight;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
