          animation-timing-function: ease-out;
  overflow: hidden;
}

.map__google {
  position: relative !important;
}

/* SKILLS */

.skills {
  margin-top: -1px;
  height: 100vh;
  position: relative;
  z-index: 1;
  background: -webkit-linear-gradient(bottom, rgba(0, 29, 196, 0.801) 0%,  rgb(17, 156, 236) 70%);
  background: linear-gradient(to top, rgba(0, 29, 196, 0.801) 0%,  rgb(17, 156, 236) 70%);
}

.skillCard {
  width: 17vw;
  height: 55rem;
  border-radius: 10px;
  -webkit-transition: -webkit-transform 0.25s ease-out;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.skillCard h2 {
  text-transform: uppercase;
  font-size: 3rem;
}

.skillCard p {
  margin-top: 2rem;
  font-size: 3rem;
}
.skillCard:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 10px 10px 8px rgba(0, 0, 0, 0.308)
}
.skillBlock {
  
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

img {
  height: 10rem;
  width: auto;
  margin-bottom: 1rem;
}

.javascript {
  height: 8rem;
}

.particles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}


/* Latest Works */

.latest-works {
