.App {
  text-align: center;
  transition: all 1s;
  background:rgba(0, 29, 196, 0.801);
}

.container {
  margin: 0 auto;
  height: 100%;
  width: 70vw;
  position: relative;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.hidden {
  display: none;
  width: 0;
  height: 0;
}

/* MAIN */

.main {
  display: flex;
  position: relative;
  justify-content: space-around;
  align-items: stretch;
  flex-wrap: wrap;
  height: 100vh;
  background-image: 
      linear-gradient(
      to right bottom, 
      rgba(71, 71, 71, 0.6),
      rgba(5, 92, 255, 0.6)
      ),
       url(./img/160632.jpg);
  background-size: cover;
  background-position: top;
  color: rgb(223, 235, 238);
}


.main__comp {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-top: 20rem;
}

.main__text p {
  font-size: 5rem;
}
.main__h{
  font-size: 5rem;
  color: rgb(255, 255, 255);
}
.main__text {
  vertical-align: center;
  animation-name: moveInRight;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}
.main__photo {
  width: 20vw;
  min-width: 25rem;
  vertical-align: top;
  animation-name: moveInLeft;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.main__content {
  width: 50vw;
}

.hire-btn {
  margin-top: 10rem;
  text-transform: uppercase;
  background: transparent;
  padding: 3rem 30rem;
  display: inline-block;
  border-radius: 10rem;
  transition: all .2s;
  position: relative;
  font-size: 2rem;
  font-weight: bold;
  border: 2px solid white;
  color: white;
  animation: moveInBottom .5s ease-out .75s;
  animation-fill-mode: backwards;
}

.hire-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 1rem 2rem rgba(0,0,0,.2);
  background: white;
  color: black;
}

.hire-btn:active {
  transform: translateY(-1px);
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
}

.photo { 
  border: 10px solid rgba(255, 255, 255, 0.473);
  min-width: 150px;
  width: 25rem;
  height: auto;
}

.lang {
  display: flex;
  justify-content: flex-end;
  animation-name: moveInRight;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.btn {
  border: 1px solid white;
  background: transparent;
  padding: 1rem 2rem;
  color:white;
  font-size: 1.5rem;
}
.en {
  margin-right: -1px;
}

.btn:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}

.active {
  background: white;
  color: black;
}
/* POP UP */ 

.pop-up {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index:100;
  background: rgba(2, 20, 39, 0.479);
  transition: all 1s;
  animation-name: backAppear;
  animation-duration: .7s;
  animation-timing-function: ease-out;
}

.pop-up__window {
  padding: 5rem;
  background: white;
  border-radius: 10px;
  width: 50rem;
  height: 35rem;
  animation-name: appear;
  animation-duration: .7s;
  animation-timing-function: ease-out;
  position: relative; 
  color: black;
  font-size: 2rem;
}

.mail__box {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 3rem;
}
.mail {
  width: 2rem;
  height: auto;
  margin: 0 1rem;
}
.phone__box {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 3rem;
}
.phone {
  width: 2rem;
  height: auto;
  margin: 0 1rem;
}

.social {
  display: flex;
  justify-content: space-around;
  margin: 3rem 0;
}

span {
  display: none;
}

.close-btn {
  position: absolute;
  right: -3.2rem;
  top: -3.2rem;
  background: url(img/logo/close.svg) no-repeat 50% 50%;
  width: 5rem;
  height: 5rem;
  border: none;
}
.close-btn:hover {
  background: url(img/logo/close1.svg) no-repeat 50% 50%;
  cursor: pointer;
}

.vk {
  background: url(img/logo/vk.svg) no-repeat 50% 50%;
  width: 7rem;
  height: 7rem;
  transition: all .6s;
}

.vk:hover {
  background: url(img/logo/vk1.svg) no-repeat 50% 50%;
  transform: scale(1.2);
}
.git {
  background: url(img/logo/github.svg) no-repeat 50% 50%;
  width: 6rem;
  height: 6rem;
  transition: all .6s;

}

.git:hover {
  background: url(img/logo/github1.svg) no-repeat 50% 50%;
  transform: scale(1.2);
}

.tw {
  background: url(img/logo/twitter.svg) no-repeat 50% 50%;
  width: 7rem;
  height: 7rem;
  transition: all .6s;

}

.tw:hover {
  background: url(img/logo/twitter1.svg) no-repeat 50% 50%;
  transform: scale(1.2);
}


/* ABOUT ME */

.about-me {
  height: 100vh;
  padding-top: 5rem;
  position: relative;
  background: linear-gradient(to bottom, rgba(22, 45, 175, 0.753) 0%, rgb(17, 156, 236) 70%);
}

.about-me__comp {
  height: 100%;
}

.about-me__h {
  display: block;
  margin: 0;
  padding: 0;
  font-size: 5rem;
  color:  rgb(0, 162, 255);
  animation-name: moveInBottom;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.about-me__content {
  margin-top: 10rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.about-me__text {
  width: 35vw;
  margin-right: 5rem;
  font-size: 4.5rem;
  text-align: left;
  color: rgb(21, 56, 109);
  animation-name: moveInLeft;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  
}

.map {
  width: 50rem;
  height: 50rem;
  animation-name: moveInRight;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  overflow: hidden;
}

.map__google {
  position: relative !important;
}

/* SKILLS */

.skills {
  margin-top: -1px;
  height: 100vh;
  position: relative;
  z-index: 1;
  background: linear-gradient(to top, rgba(0, 29, 196, 0.801) 0%,  rgb(17, 156, 236) 70%);
}

.skillCard {
  width: 17vw;
  height: 55rem;
  border-radius: 10px;
  transition: transform 0.25s ease-out;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.skillCard h2 {
  text-transform: uppercase;
  font-size: 3rem;
}

.skillCard p {
  margin-top: 2rem;
  font-size: 3rem;
}
.skillCard:hover {
  transform: scale(1.05);
  box-shadow: 10px 10px 8px rgba(0, 0, 0, 0.308)
}
.skillBlock {
  
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

img {
  height: 10rem;
  width: auto;
  margin-bottom: 1rem;
}

.javascript {
  height: 8rem;
}

.particles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}


/* Latest Works */

.latest-works {
  margin-top: ;
}
.latest-works {
  height: 100vh;
  background: linear-gradient(to bottom, rgba(0, 29, 196, 0.801) 0%,  rgb(2, 18, 90) 70%);
}

.latest-works__box {
  position: relative;
  width: 70vw;
  height: 100%;
  padding-top: 10rem;
}

.latest-works__slider {
  display: flex;
  width: 100%;
}

.latest-works__h {
  margin: 0;
  margin-bottom: 5rem;
  padding: 0;
  font-size: 5rem;
  color:  rgb(0, 162, 255);
  animation-name: moveInBottom;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.work-card {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  overflow: hidden;
  width: 100rem;
  height: 70rem;
  border-radius: 5px;
  flex: 1 0 100%;
  flex-basis: 100%;
  margin-right: 20px;
  animation-name: appear;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.work-card__text {
  display: none;
  opacity: 0;
  transition: all 3s;
}

.work-card:hover .work-card__content {
  height: 100%;
  color: white;
  background: rgba(31, 6, 88, 0.705);
}

.work-card:hover .work-card__text {
  display: block;
  color: white;
  font-size: 4rem;
  opacity: 1;
}

.work-card:hover .work-card__git, .work-card:hover .work-card__page{
  display: block;
}
.work-card__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 30%;
  font-size: 6rem;
  background: rgba(44, 6, 6, 0.521);
  color: rgb(0, 55, 206);
  border-radius: 5px;
  transition: all 1.5s;
  padding: 0 10rem;
}

.work-card__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.work-card__git {
  background: url(img/logo/github.svg) no-repeat 50% 50%;
  width: 6rem;
  height: 6rem;
  transition: all .6s;
  display: none;
  
}

.work-card__git:hover {
  background: url(img/logo/github1.svg) no-repeat 50% 50%;
  transform: scale(1.2);
}

.work-card__page{
  background: url(img/logo/link-symbol.svg) no-repeat 50% 50%;
  width: 6rem;
  height: 6rem;
  transition: all .6s;
  margin-right: 5rem;
  display: none;
}

.work-card__page:hover {
  background: url(img/logo/link.svg) no-repeat 50% 50%;
  transform: scale(1.2);
}

.buttons {
  position: absolute;
  top: 50rem;
  left: -2.5%;
  width: 105%;
  display: flex;
  justify-content: space-between;
}

.btn-left {
  background: url(img/logo/left-arrow.svg) no-repeat 50% 50%;
  width: 5rem;
  height: 5rem;
  border: none;
}

.btn-right {
  background: url(img/logo/right-arrow.svg) no-repeat 50% 50%;
  width: 5rem;
  height: 5rem;
  border: none;
}

.btn-box:hover {
  transform: scale(1.2);
  background:  rgba(100, 237, 255, 0.534);
  cursor: pointer;
}

.btn-slider {
  cursor: pointer;
}

.btn-box:hover .btn-right {
  background: url(img/logo/right-arrow1.svg) no-repeat 50% 50%;
}

.btn-box:hover .btn-left {
  background: url(img/logo/left-arrow1.svg) no-repeat 50% 50%;
}

.btn-slider:active{
  transform: scale(0.8);
}

.btn-box {
  background:  rgba(12, 0, 78, 0.534);
  border-radius: 50%;
  padding: 1rem;
  transition: all 1s;
  animation-name: appear;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

.faceapp {
  background:linear-gradient(
    to right bottom, 
    rgba(56, 56, 56, 0.37),
    rgba(84, 91, 104, 0.37)
    ), url(img/faceapp.jpg) no-repeat 50% 50%;
  background-size: cover;

}

.robofriends {
  background:linear-gradient(
    to right bottom, 
    rgba(56, 56, 56, 0.37),
    rgba(84, 91, 104, 0.37)
    ), url(img/robofriends.jpg) no-repeat 50% 50%;
  background-size: cover;
}




/* KEYFRAMES */

@keyframes appear {
  0% {
      opacity: 0;
      transform: scale(0.1);
  }
  80% {
      opacity: 0.8;
      transform: scale(1.2);
  }
  100% {
      opacity: 1;
      transform: scale(1);
  }

}

@keyframes backAppear {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes moveInBottom {
  0% {
      opacity: 0;
      transform: translateY(-10rem);
  }

  80% {
      transform: translateY(1rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}

@keyframes moveInLeft {
  0% {
      opacity: 0;
      transform: translateX(-10rem);
  }

  80% {
      transform: translateX(1rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
      opacity: 0;
      transform: translateX(10rem);
  }

  80% {
      transform: translateX(-1rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}



@keyframes moveInBottom {
  0% {
      opacity: 0;
      transform: translateY(10rem);
  }

  80% {
      transform: translatey(-1rem);
  }

  100% {
      opacity: 1;
      transform: translate(0);
  }
}








